import React, {Component} from 'react';
import Typist from 'react-typist';

class WorkExperience extends Component {
    render() {
        return (
            <div>
                <div>
                    <div class="gradient3 jumbotron jumbotron-fluid">
                        <div class="container ">
                            <Typist
                                className="Typist"
                                cursor={{show: false}}
                            >
                                <span className="my-custom-class"><h1 class="display-3">Work Experience</h1></span>

                            </Typist>
                        </div>
                    </div>
                    <div>
                        <center>
                            <div className="container">
                                <div className="row">
                                    <div className="col-6">
                                        Although I have no work experience as of now, my participation in the Dublin
                                        High School Engineering Academy has given me many valuable experiences. One of
                                        the opportunities the academy offers was many field trips, although sometimes my
                                        schedule was not permitting, I still was able to go on one to Stanford and the
                                        Computer Science History Museum. This field trip helped open up my eyes to what
                                        was to be expected when applying to college and the process behind it, as well
                                        as the college culture in a prestigious college. The Computer Science History
                                        Museum had displays of old technology and gave me an appreciation of how
                                        technology evolved to where we are now.
                                    </div>
                                    <div className="col-6">
                                        <img src={require('../IMG_1131.jpg')}
                                             style={{maxWidth: '75%', maxHeight: '75%'}}/>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-12">
                                        The Principles of Engineering class has also hosted many guest speakers. One
                                        such memorable speaker was Aaron Pomerantz, PhD candidate at UC Berkeley who was
                                        a field biologist. This guest speaker was a type of engineer who didnt work in
                                        the office, but also in the field, and helped me realize the size and
                                        application of engineering in the world.
                                    </div>
                                </div>
                            </div>
                        </center>
                    </div>
                </div>
            </div>
        )
    }
}

export default WorkExperience


